export let code_ready = `
/* 
 * 你好，我是易畅。
 * 我感觉静态的简历有些乏味，于是我想用我所学动态地展示我的简历。
 * 现在开始...
 */

/* 添加过度动画，使代码变化更柔和 */
*{
  transition:all 1.5s;
}
/* 添加背景色 */
body {
  background: #455A64;
}
/* 添加代码边框 */
#code_body {
  width: 100%;
  border: 1px solid #CFD8DC;
  background: #CFD8DC;
  padding: 16px;
  overflow: auto;
}

/* 代码高亮 */
.token.comment, .token.punctuation {
  color: #757575;
}
.token.selector {
  color: #007400;
}
.token.property {
  color: #cf1f1f;
}

/* 
 * 加特效：
 */
.breathe {
  animation: breathe 1s ease 0s infinite;
}


/* 现在开始写简历 */

/* 重新编排布局 */
#options {
  width: 32%;
}
/* 准备空白画布 */
#paper {
  flex: 1;
  background: #fdfdfd;
  margin: 16px;
  overflow: auto;
  white-space: pre-wrap;
}

/* 现在请看右侧 */
`