import $ from 'jquery';
import marked from "marked";
import {code_ready} from "./codeReady/style";
import {resume} from "./codeReady/resume";
import {addMarkedjs} from "./codeReady/addMarkedjs";
import {beautifyResume} from "./codeReady/beautifyResume";
import {addPhoto} from "./codeReady/addPhoto";

const speedStatus = document.getElementById("speedStatus");
const code_body = document.getElementById("code_body");
const code_style = document.getElementById("code_style");
const paper = document.getElementById("paper");

let codeInputTimeoutId = undefined;
let resumeInputTimeoutId = undefined;

let speedCase = 2, time = 100;
$("#btnSpeed").click(() => {
    speedCase++;
    if (speedCase > 4) {
        speedCase = 1
    }
    switch (speedCase) {
        case 1:
            time = 100;
            speedStatus.innerText = "0.5×";
            break;
        case 2:
            time = 50;
            speedStatus.innerText = "1.0×";
            break;
        case 3:
            time = 25;
            speedStatus.innerText = "1.5×";
            break;
        case 4:
            time = 0;
            speedStatus.innerText = "2.0×";
            break;
        default:
            return 0;
    }
})

function writeCode(code, origin) {
    let n = 1
    return new Promise(resolve => {
        codeInputTimeoutId = setTimeout(write, time)

        function write() {
            code_body.innerHTML = Prism.highlight(origin + code.substr(0, n), Prism.languages.css, 'css');
            code_style.innerHTML = origin + code.substr(0, n);
            code_body.scrollTop = code_body.scrollHeight;
            n += 1;
            console.log("hello");
            if (n > code.length) {
                window.clearTimeout(codeInputTimeoutId);
                codeInputTimeoutId = undefined;
                resolve.call(undefined)
            } else {
                window.clearTimeout(codeInputTimeoutId);
                codeInputTimeoutId = setTimeout(write, time);
            }
        }
    })
}

function writeResume() {
    let n = 1;
    return new Promise(resolve => {
        resumeInputTimeoutId = setTimeout(write, time);

        function write() {
            paper.innerHTML = resume.substr(0, n);
            paper.scrollTop = paper.scrollHeight;
            n += 1;
            if (n > resume.length) {
                window.clearTimeout(resumeInputTimeoutId);
                resumeInputTimeoutId = undefined;
                resolve.call(undefined);
            } else {
                window.clearTimeout(resumeInputTimeoutId);
                resumeInputTimeoutId = setTimeout(write, time);
            }
        }
    })
}

function skipResumeInput() {
    return new Promise(resolve => {
        setTimeout(() => {
            window.clearTimeout(resumeInputTimeoutId);
            paper.innerHTML = resume;
            paper.scrollTop = paper.scrollHeight;
            resolve();
            $("#skip_inputResume").remove();
        }, 0);
    })
}

function createResume() {
    return new Promise(resolve => {
        $("#paper").addClass("breathe");
        $("#code_body").removeClass("breathe");
        writeResume().then(resolve);
        setTimeout(() => {
            $("#skip_inputResume").show();
            $("#skip_inputResume").click(() => {
                skipResumeInput().then(resolve)
            });
        }, 1000);
    })
}

function structureResume() {
    $("#paper")[0].innerHTML = marked(resume);
    $("#paper").prepend(`
        <div id="information"></div>
        <div id="education"></div>
        <div id="projects"></div>
        <div id="skills"></div>
        <div id="openSource"></div>
        <div id="others"></div>
    `);

    const inforHeader = $('h1').first()
    $("#information").append(inforHeader, inforHeader.next("ul")[0]);

    $('#projects').append($('h2:contains("项目")'));
    const project_title_array = $('#paper > h3').slice(0, 6);
    project_title_array.each((index, element) => {
        $('<div class="single"></div>')
            .append($(element), $(element).next('p')[0], $(element).next('p').next('ul')[0])
            .appendTo('#projects');
    });

    const skillsTitle = $("h2:contains('技能')")
    $("#skills").append(skillsTitle, skillsTitle.next("ul")[0]);


    $('#openSource').append($('h2:contains("其他作品")'));
    const openSource_title_item = $('#paper > h3').slice(0, 3);
    openSource_title_item.each((index, element) => {
        $('<div class="single"></div>')
            .append($(element), $(element).next('p')[0], $(element).next('p').next('ul')[0])
            .appendTo('#openSource');
    });

    const othersHeader = $("h2:contains('相关链接')")
    $("#others").append(othersHeader, othersHeader.next("ul")[0]);

    $("#education").append($("h2:contains('教育')"), $("h2:contains('教育')").next("ul")[0]);
}

function skipAll() {
    return new Promise(resolve => {
        setTimeout(() => {
            window.clearTimeout(codeInputTimeoutId);
            codeInputTimeoutId = undefined;
            resolve();
            $(".skipper").remove();
        }, 0);
    })
}

function adjustResume() {
    return new Promise((resolve, reject) => {
        $("#code_body").addClass("breathe");
        $("#paper").removeClass("breathe");

        writeCode(addMarkedjs, code_ready)
            .then(() => {
                structureResume();
                $('#paper')[0].scrollTop = 0;

                writeCode(beautifyResume, code_ready + addMarkedjs).then(resolve);
                console.log(code_ready + addMarkedjs);
                setTimeout(() => {
                    $('#skip_all').show()
                    $('#skip_all').click(() => {
                        skipAll().then(reject);
                    })
                }, 1000);
            });
    })
}

function beautifyPaper() {
    return new Promise(resolve => {
        $("#information").append($("img.avatar"));
        writeCode(addPhoto, code_ready + addMarkedjs + beautifyResume)
            .then(() => {
                $('#paper').addClass('breathe');
                $('#code_body').removeClass('breathe');

                $('.skipper').remove()
                setTimeout(resolve, 1000)
            })
    })
}

function showFinalResult() {
    $('#paper').addClass('breathe');
    $('#code_body').removeClass('breathe');
    $("#information").append($("img.avatar"));

    $("a.downloadResume").addClass("display");
    $("#options").append($("a.downloadResume"));
    $("#btnSpeed").remove();

    code_body.innerHTML = Prism.highlight(code_ready + addMarkedjs + beautifyResume + addPhoto, Prism.languages.css, "css");

    code_style.innerHTML = code_ready + addMarkedjs + beautifyResume + addPhoto;

    code_body.scrollTop = code_body.scrollHeight;

    window.clearTimeout(codeInputTimeoutId);
    codeInputTimeoutId = undefined;
}

function showDownloadButton() {
    $("a.downloadResume").addClass("display");
    $("#options").append($("a.downloadResume"));
}

writeCode(code_ready, "")
    .then(() => {
        createResume()
            .then(() => {
                $("#skip_inputResume").remove();
                adjustResume().then(() => {
                    beautifyPaper()
                        .then(() => {
                            showDownloadButton();
                            $("#btnSpeed").remove();
                            code_body.scrollTop = code_body.scrollHeight;
                        });
                }, () => {
                    showFinalResult();
                });
            });
    });