export let beautifyResume = `
/* 调整我的简历，让它变得再好看一点 */
#paper {
    white-space: unset;
    padding: 0 32px;
    font-size: 14px;
}

#paper a {
    color: #455A64;
}

/* 让每个模块上下间隔明显一点 */
#paper > div {
    margin: 28px 0;
}

h1 {
    font-size: 35px;
    margin-bottom: 10px;
}

p {
    margin: .3em 0;
}

ul, ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

#paper h2, h3 {
    margin-bottom: 6px;
}

h4, h5, h6 {
    margin: 0;
}

h4, h5, h6 {
    font-weight: normal;
}

a {
    color: #999;
    text-decoration: none;
    /*border-bottom: 1px solid;*/
}

a:hover {
    border-bottom: 2px solid;
    color: #333;
}

li, p {
    color: #666;
    font-size: 16px;
    margin-bottom: 4px;
}

strong {
    color: #f60;
}

#information {
    position: relative;
}

#information:after {
    content: '';
    display: block;
    clear: both;
}

#information ul {
    padding-left: 0px;
}

#projects > .single {
    position: relative;
}

#projects p {
    position: absolute;
    right: 0;
    top: 0;
}

#openSource > .single {
    position: relative;
}

#openSource p {
    position: absolute;
    right: 0;
    top: 0;
}
`;